import React from "react";
import {
  makeStyles,
  Card,
  CardActionArea,
  CardContent,
  Typography,
  CardActions,
  Button,
  Container,
  

} from "@material-ui/core";

import Grid from "@material-ui/core/Grid";

import Divider from '@material-ui/core/Divider';

import storeProducts from "../../../data";

const useStyles = makeStyles (theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    overflow: "hidden",
    flexDirection: "row"
  },
  rowtest: {
    display: "flex",
    flexDirection: "row",
    width: "100%"
  },
  card: {
    maxWidth: 270,
    
  },
  media: {
    height: 150,
    width: 60,
    justifyItems: "center"
  },
  gridList: {
    width: "auto",
    height: "auto"
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)"
  },
  modal: {
    display: 'flex',
    padding: theme.spacing(1),
    alignItems: 'center',
    justifyContent: 'center',
  },
 
}));

export default function Men(props) {
  const locationRef = props.location.pathname.slice(1);
  const productsByRef = storeProducts.filter(ref => {
    if(ref.category === locationRef) {
      return ref;
    }
  });
  //console.log(productsByRef)
  const classes = useStyles();
  //console.log(storeProducts);
  return (
<div>
<Container fixed>
    <Grid container spacing={8}>
 
      <Grid item xs={12}>
        <Typography
          gutterBottom
          variant="h4"
          component="h2"
          style={{ textAlign: "center", paddingTop: "2.2%" }}
        >
          Products
        </Typography>
        <Divider orientation="horizontal"  />
      </Grid >
      
      

      {productsByRef.map((product, index) => (
        
        <Grid item xs={6} sm={4} key={index}>

          <Card className={classes.card} elevation="5" >
            <CardActionArea>
              <img
                src={product.image}
                alt={product.title}
                style={{ width: 250  }}
              />
              <CardContent>
                <Typography gutterBottom variant="h7" component="h4">
                  {product.title}
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" color="primary" >
                Buy Now
              </Button>
              <text justify="flex-end">Rs{product.price}</text>
            </CardActions>
          </Card>
        </Grid>
        
      ))}

    </Grid>
    </Container>
    </div>
  );
}


