import React from "react";
import {
  AppBar,
  Toolbar,
  Typography,
  MenuItem,
  makeStyles,
  IconButton
} from "@material-ui/core/";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import { Switch, Route, Link } from "react-router-dom";
import "./style.css";
import styled from "styled-components";
import Home from "../Page/Home/index";
import Men from "../Page/ProductPage/Men";
import Women from "../Page/ProductPage/Women";
import Kids from "../Page/ProductPage/kids";
import CartList from "../Cart/CartList";
import Products from "../Page/ProductPage/index";

const useStyles = makeStyles(theme => ({
  title: {
    flexGrow: 1
  }
}));

export default function ButtonAppBar() {
  const classes = useStyles();

  return (
    <div >
      <Nav className="navbar navbar-expand-sm  navbar-dark px-sm-5" style={{ padding: "7px" }}>
        <AppBar
          position="relative"
          color="default"
          style={{
            flexGrow: 1,
            display: "flexGrow",
            justifyContentnt: "flexGrow"
          }}
        >
          <Toolbar>
            <Link to="/" style={{ color: "black" }}>
              <Typography
                style={{
                  marginLeft: "-2%"
                }}
                variant="overline"
                component="menu"
                color="inherit"
                gutterBottom
              >
                <MenuItem>Home</MenuItem>
              </Typography>
            </Link>

            <Link to="/products" style={{ color: "black" }}>
              <Typography
                variant="overline"
                component="menu"
                color="inherit"
                gutterBottom
              >
                <MenuItem>Product</MenuItem>
              </Typography>
            </Link>

            <Link to="#" style={{ color: "black" }}>
              <Typography
                variant="overline"
                component="menu"
                color="inherit"
                gutterBottom
              >
                <MenuItem>Contact us</MenuItem>
              </Typography>
            </Link>

            <Typography
              variant="overline"
              component="menu"
              color="inherit"
              gutterBottom
              className={classes.title}
            >
              <Link to="/" style={{ color: "black" }}>
                <MenuItem>About</MenuItem>
              </Link>
            </Typography>

            <Link to="/cart" style={{ color: "black" }}>
              <IconButton>
                <ShoppingCartIcon type="button" color="inherit" />
              </IconButton>
            </Link>
          </Toolbar>
        </AppBar>
      </Nav>
      <div>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/men" component={Men} />
          <Route path="/cart" component={CartList} />
          <Route path="/women" component={Women} />
          <Route path="/kids" component={Kids} />
          <Route path="/products" component={Products} />

          <Route
            render={function() {
              return (
                <p>
                  404...
                  <br />
                  page Not found
                </p>
              );
            }}
          />
        </Switch>
      </div>
    </div>
  );
}
const Nav = styled.nav`
text-shadow-offset: 10px 5px;  
background: ;
  .nav-link {
    color: var(--mainWhite) !important;
    font-size:1.3rem;
    text-transform:capitalize;
  }
  @media (max-width: 576px) {
    .navbar-nav {
      flex-direction: row !important;
`;
